<template>
  <div id="industry">
    <!--  -->
    <mu-dialog :open="add_dialog" title="" @close="add_close">
      <mu-text-field hintText="" v-model="add_name" fullWidth/><br/>
      <mu-flat-button slot="actions" @click="add_close" primary label=""/>
      <mu-flat-button slot="actions" primary @click="add" label=""/>
    </mu-dialog>

    <!--  -->
    <mu-dialog :open="edit_dialog" title="" @close="edit_close">
      <mu-text-field hintText="" v-model="editing.name" fullWidth/><br/>
      <mu-flat-button slot="actions" @click="edit_close" primary label=""/>
      <mu-flat-button slot="actions" primary @click="editSubmit" label=""/>
    </mu-dialog>

    <!--  -->
    <mu-dialog :open="del_dialog" title="" @close="del_close">
      {{deling.name}}<br/>
      <mu-flat-button slot="actions" @click="del_close" primary label=""/>
      <mu-flat-button slot="actions" primary @click="delSubmit" label=""/>
    </mu-dialog>

    <div class="warp">
      <div class="header">
        <!--  -->
        <mu-text-field hintText="" icon="search" class="search"/>
        <!--  -->
        <mu-float-button icon="add" mini class="demo-float-button" @click="add_dialog = true"/>
      </div>
      <!--  -->
      <div class="list">
        <el-chip v-for="(v,i) of data" @edit="edit(i, v)" class="item" @delete="del(i, v)">{{v.name}}</el-chip>
      </div>
    </div>
    
  </div>

</template>

<script>
import Chip from '@/components/chip'

export default {
  components: {
    'el-chip': Chip
  },
  data () {
    return {
      add_dialog: false, // 
      chip_hover: '',
      add_name: '',
      data: [],
      edit_dialog: false, // 
      editing: { // 
        index: -1,
        id: 0,
        name: ''
      },
      deling: { // 
        index: -1,
        id: 0,
        name: ''
      },
      del_dialog: false // 
    }
  },
  created: function () {
    this.get()
  },
  methods: {
    editSubmit: function () {
      var index = this.editing.index
      var url = this.api_path('industry/update/') + this.editing.id
      this.axios.post(url, {
        id: this.editing.id,
        name: this.editing.name
      })
      .then(res => {
        this.$set(this.data[index], 'name', this.editing.name) // 
        this.success(res.data.message)
        this.edit_close()
      })
      .catch(err => {
        this.failed(err.data.message)
      })
    },
    edit: function (index, data) {
      this.editing = {
        index: index,
        id: data.id,
        name: data.name
      }
      this.edit_dialog = true
    },
    // 
    edit_close: function () {
      this.edit_dialog = false
    },
    // 
    delSubmit: function () {
      var index = this.deling.index // index
      var id = this.deling.id // id
      this.message('')
      var url = this.api_path('industry/del/') + id
      this.axios.get(url)
      .then(res => {
        this.$delete(this.data, index)
        this.success(res.data.message)
        this.del_close() // 
      })
      .catch(err => {
        this.del_close()
        this.failed(err.message)
      })
    },
    // 
    del_close: function () {
      this.del_dialog = false
    },
    del: function (index, industry) {
      this.deling = {
        index: index,
        id: industry.id,
        name: industry.name
      }
      this.del_dialog = true
    },
    get: function () {
      var url = this.api_path('industry')
      this.axios.get(url)
      .then(res => {
        this.data = res.data
      })
      .catch(res => {
        this.message('')
      })
    },
    add: function () {
      this.message('')
      var url = this.api_path('industry/add')
      this.axios.post(url, {name: this.add_name})
      .then(res => {
        this.add_name = ''
        this.get()
        this.add_close()
        this.success('')
      })
      .catch(err => {
        this.failed(err.message)
      })
    },
    hover: function () {
      // this.chip_hover = 'hover'
      alert('sdfdsfdsf')
    },
    handleClose () {
      window.alert('')
    },
    add_close () {
      this.add_dialog = false
    }
  }
}
</script>

<style lang="scss" scoped>
#industry {
  margin: 20px;

  .warp {
    box-shadow: 0 1px 6px rgba(0,0,0,.117647),0 1px 4px rgba(0,0,0,.117647);
    border-radius: 5px;

    .header {
      background-color: #e8e8e8;
      height: 60px;
      border-radius: 5px 5px 0 0;
      display: flex;
      justify-content: space-between;
      align-items: center;
      padding-right: 20px;

      .search {
        margin: 5px 0 0 0;
      }
    }

    .list {
      padding: 15px;
      border: 1px solid #ddd;
      border-radius: 0 0 5px 5px;

      .item {
        margin: 10px;
      }
    }
  }

}
</style>


